.container {
  width: 80%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;

  @media (max-width: 760px) {
    .container {
      justify-content: space-around;
      align-items: normal;
      align-content: center
    }
  }
}